.btnStd {
padding: 20px 30px;
background-color: black;
color: white;
font-size: 1em;
font-weight: 900;
border: none;
border-radius: 1em;
margin-top: 30px;
cursor: pointer;
width: 100px;
display: flex;
flex-flow: row wrap;
justify-content: center;
align-items: center;
margin: 10px;
}

.btnStd:active {
  box-shadow: 10px 10px 65px -10px rgba(0, 0, 0, 0.55);
}